export default class Export {
    constructor(element) {
        this.element = element;
        this.disable();
        this.initEvents();
        this.checkState();
    }
    initEvents() {
        this.element.querySelectorAll('input[type=checkbox]').forEach(checkbox => {
            checkbox.addEventListener('change', () => this.checkState());
        });
    }
    disable() {
        this.element.querySelectorAll('button[data-export]').forEach(button => button.setAttribute('disabled', '1'));
    }
    enable() {
        this.element.querySelectorAll('button[data-export]').forEach(button => button.removeAttribute('disabled'));
    }
    checkState() {
        let checked = 0;
        this.element.querySelectorAll('input[name="list_screen_ids[]"]').forEach(el => {
            if (el.checked) {
                checked++;
            }
        });
        if (checked > 0) {
            this.enable();
        }
        else {
            this.disable();
        }
    }
}
