export default class Import {
    constructor(element) {
        this.element = element;
        this.disable();
        this.initEvents();
    }
    initEvents() {
        let fileInput = this.element.querySelector('input[type=file]');
        if (fileInput) {
            fileInput.addEventListener('change', () => {
                fileInput.value ? this.enable() : this.disable();
            });
        }
    }
    disable() {
        this.element.querySelectorAll('input[type="submit"]').forEach(button => button.setAttribute('disabled', '1'));
    }
    enable() {
        this.element.querySelectorAll('input[type="submit"]').forEach(button => button.removeAttribute('disabled'));
    }
}
